import React from "react";
import { Route, Routes } from "react-router-dom";
//main
import Main from './routes/Main';
//sub
import AllGames from './routes/sub/AllGames'

import './App.css';




function App() {
  return (
    <Routes>
         <Route exact path='/helmet' element={<Main/>} />
         <Route
             path="/helmet/game/all_games"
             exact={true}
             element={<AllGames/>}
         />
    </Routes>
  );
}

export default App;
